.cloud-container{
  width: 100%;
  min-width: 500px;
  min-height: 500px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #40b0bd;
  /* background: linear-gradient(to bottom, #40b0bd 1%, #40b0bd 100%); */
}

#cloud-intro {
  background: url(/portfolio/static/media/clouds.9c9bd8d6.png) repeat-x top center;
  background-size:contain;
  background-repeat:repeat;
  background-color:#41b0bf;
  position:relative;
}

.cloud-container .name{
    font-family: 'Great Vibes', sans-serif;
    font-weight: 400;
    font-size: 80px;
    color: #e8f1ed;
}

.cloud-container .sub-name{
    font-family: 'Poppins', sans-serif;
    color: #e8f1ed;
    font-size: xx-large;
}

.cloud-container .outlined-white{
    font-family: 'Poppins', sans-serif;
    background-color: #41b0bf;
    color: #fff;
    border-radius: 30px;
    padding: 15px 25px;
    border: 1px solid #fff;
    font-size: 14px;
    letter-spacing: 1px;
}

.cloud-container .outlined-white:hover{
    background: #fff;
    color: #41b0bf;
}
.figure{
    display: -webkit-flex !important;
    display: flex !important;
}

.profile-pic{
    border-radius: 50% !important;
    margin: 10px;
    width:250px !important;
    height:200px !important;
}

.figure .figure-caption{
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    color: #838383;
}

i {
    font-size: 40px;
    color: #3b5999;
    padding: 15px;
}

